import { Component, OnInit } from '@angular/core';
import { CategoryService } from '../../../../../auth/_services/category.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from "@angular/router";

@Component({
    selector: 'app-category-list',
    templateUrl: "category-list.component.html",
    styles: []
})
export class CategoryListComponent implements OnInit {
    form: FormGroup;
    private formSumitAttempt: boolean;

    constructor(private router: Router, private toastr: ToastrService, private _categoryService: CategoryService, private formBuilder: FormBuilder) { }
    public categoryData;
    public searchValue = "";
    public dropDownValue = '';
    public noOfPage = '';
    public menuDrpdown: any;
    public selectedMenu = '';
    public selectedStatus = '';
    p: number = 1;
    ngOnInit() {

        /*user permission*/
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let permission = currentUser.success.permission;
        if (permission.product_category != 1) {
            this.toastr.error('You have not permission to visit this page.', 'Error Message');
            this.router.navigate(['/']);
        }
        /*user permission*/

        localStorage.setItem('section', 'category');
        this._categoryService.getAll().subscribe(
            data => {
                this.categoryData = data.success;
                this.menuDrpdown = data.menu;
                console.log(this.menuDrpdown);
            }
        );


        this.form = this.formBuilder.group({
            menu_id: [null, Validators.required],
            category_name: [null],
            status: [null, Validators.required]
        });
    }



    isFieldValid(field: string) {
        return (
            (!this.form.get(field).valid && this.form.get(field).touched) ||
            (this.form.get(field).untouched && this.formSumitAttempt)
        );
    }

    displayFieldCss(field: string) {
        return {
            'has-error': this.isFieldValid(field),
            'has-feedback': this.isFieldValid(field)
        };
    }

    onSubmit() {
        this.formSumitAttempt = true;
        if (this.form.valid) {
            let that = this;
            this._categoryService.getByName(this.form.value.category_name)
                .subscribe(
                searchData => {
                    if (searchData.success) {
                        this.toastr.error('Category Already Exist', 'Error Message');
                    } else {
                        this._categoryService.createCategory({ form_data: this.form.value }).subscribe(
                            data => {
                                this.categoryData = data.data;
                                this.toastr.success('Category Added Successfully', 'Success Message');
                                this.reset();
                            }
                        );
                    }
                }
                );

        }
    }

    reset() {
        /*this.form.reset();
        this.formSumitAttempt = false;*/
        if (this.selectedMenu != '' && this.selectedStatus != '') {
            this.form.reset();
            this.selectedMenu = '';
            this.selectedStatus = '';
            this.formSumitAttempt = false;
        }
    }



    public category_delete(del_id) {

        console.log(del_id);
        let value = confirm('Are u want to delete this category?');
        if (value) {
            this._categoryService.categoryDelete(del_id).subscribe(
                data => {
                    this.categoryData = data.data;

                }

            );


        }

    }


    public changeCategoryStatus(category: any) {

        console.log(category);

        if (category.status == 'Active') {
            category.status = 'Inactive';
            this._categoryService.updateStatus(category.id).subscribe(

                data => {
                    console.log('update', data);
                }
            );
        }
        else {
            category.status = 'Active';
            this._categoryService.updateStatus(category.id).subscribe(

                data => {
                    console.log('update', data);
                }
            );
        }
    }

    public search() {
        this._categoryService.searching({ search_value: this.searchValue, dropdown_value: this.dropDownValue }).subscribe(
            data => {
                this.categoryData = data.success;
            }
        );
    }





}
